/* ========================================================================================
Utilities
=========================================================================================*/
/* ---------- size --------- */
.minw-250 {
  min-width: 250px;
}

.minw-150 {
  min-width: 150px;
}

.minw-120 {
  min-width: 120px;
}

.minw-100 {
  min-width: 100px;
}

.minw-80 {
  min-width: 80px;
}

.maxw-250 {
  max-width: 250px;
}

.maxw-150 {
  max-width: 150px;
}

/* ---------- Colors --------- */
.text-primary-emphasis {
  color: #552482 !important;
}

.bg-primary-emphasis {
  background-color: #552482 !important;
}

.text-primary-light {
  color: #a271cf !important;
}

.bg-primary-light {
  background-color: #a271cf !important;
}

/* ========================================================================================
Components
=========================================================================================*/
/* -------------------- Nav ------------------- */
@media (min-width: 1200px) {
  .navbar-vertical.navbar-expand-xl .navbar-collapse {
    margin-top: 10px;
  }
  .navbar-vertical.navbar-expand-xl .navbar-collapse .nav-link {
    padding: 10px 0;
  }
  .navbar-vertical.navbar-expand-xl .navbar-collapse .nav-link.active {
    font-weight: 600;
  }
}
/* -------------------- Wizard (steps) ------------------- */
.theme-wizard .nav-item {
  padding: 0 5px;
}
.theme-wizard .nav-item .nav-link {
  font-weight: 600;
  color: #b6c1d2;
}
[data-bs-theme=light] .theme-wizard .nav-item .nav-link {
  background-color: #f9fafd;
}
[data-bs-theme=dark] .theme-wizard .nav-item .nav-link {
  background-color: #0b1727;
  color: #344050;
}
.theme-wizard .nav-item .nav-link.done {
  color: #b6c1d2;
  background-color: #748194;
}
.theme-wizard .nav-item .nav-link.active {
  background-color: #1869d2;
}

/* .theme-wizard-common-style */
.theme-wizard.theme-wizard-common-style .nav-item {
  padding: 0;
}
.theme-wizard.theme-wizard-common-style .nav-item .nav-link {
  font-weight: 600;
  color: #b6c1d2;
}
[data-bs-theme=light] .theme-wizard.theme-wizard-common-style .nav-item .nav-link {
  background-color: transparent;
}
[data-bs-theme=dark] .theme-wizard.theme-wizard-common-style .nav-item .nav-link {
  background-color: transparent;
  color: #344050;
}
.theme-wizard.theme-wizard-common-style .nav-item .nav-link.done {
  color: #b6c1d2;
  background-color: transparent;
}
.theme-wizard.theme-wizard-common-style .nav-item .nav-link.done .nav-item-circle-parent:before {
  background-color: #7e55a4;
}
.theme-wizard.theme-wizard-common-style .nav-item .nav-link.done .nav-item-circle-parent .nav-item-circle {
  background-color: #7e55a4 !important;
  border-color: #7e55a4;
}
.theme-wizard.theme-wizard-common-style .nav-item .nav-link.active {
  background-color: transparent;
  color: #b6c1d2 !important;
}
.theme-wizard.theme-wizard-common-style .nav-item .nav-link.active .nav-item-circle-parent:after {
  background-color: #7e55a4;
}
.theme-wizard.theme-wizard-common-style .nav-item .nav-link .nav-item-circle {
  height: 1.9rem;
  width: 1.9rem;
  line-height: 1.7rem;
}

/* -------------------- Avatars ------------------- */
/*on dropdown*/
.dropdown-with-avatars .dropdown-toggle {
  padding: 4px 10px;
  font-size: 14px;
  box-shadow: none;
}
[data-bs-theme=light] .dropdown-with-avatars .dropdown-toggle {
  border-color: #d8e2ee;
  color: #9da9bb;
}
[data-bs-theme=dark] .dropdown-with-avatars .dropdown-toggle {
  border-color: #344050;
}
.dropdown-with-avatars .dropdown-toggle:after {
  display: none;
}
.dropdown-with-avatars .dropdown-menu {
  max-height: 300px;
  overflow: auto;
}

/* -------------------- Dropzone ------------------- */
.dropzone.dropzone-sm {
  min-height: unset;
}
.dropzone.dropzone-sm .dz-message {
  padding: 1.5rem;
}

/* -------------------- Tables ------------------- */
.table {
  font-size: 14px;
}
.table th,
.table td {
  vertical-align: middle;
}
[data-bs-theme=light] .table th {
  color: black;
}
[data-bs-theme=light] .table.table-white {
  background-color: #fff;
}
.table .form-control {
  font-size: 14px;
}
.table .form-select {
  font-size: 14px;
}

/* -------------------- Pagination ------------------- */
.pagination .page-item .page-link {
  min-width: 26px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 5px;
  border: none;
  font-size: 12px;
  color: #1869d2;
}
[data-bs-theme=light] .pagination .page-item .page-link:hover {
  background-color: #d8e2ef;
}
[data-bs-theme=dark] .pagination .page-item .page-link:hover {
  background-color: #000;
}
[data-bs-theme=light] .pagination .page-item.active .page-link {
  background-color: #d8e2ef;
}
[data-bs-theme=dark] .pagination .page-item.active .page-link {
  background-color: #000;
}

/* -------------------- Card ------------------- */
/*
[data-bs-theme=light] .card {
	box-shadow: 0 0 10px #eee;
}
*/
/* -------------------- Customize panel ------------------- */
.setting-toggle {
  transform: rotate(-90deg);
  margin: 0;
  border-radius: 20px 20px 0 0;
}
.setting-toggle .card-body {
  padding: 5px !important;
}
.setting-toggle .card-body .bg-primary-subtle {
  background-color: transparent !important;
}
.setting-toggle .card-body .text-uppercase {
  display: none;
}

/* -------------------- Breadcrumb ------------------- */
.breadcrumb {
  font-size: 13px;
}

/* -------------------- Forms ------------------- */
.form-control.form-control-style-cursor {
  border-top: none;
  border-bottom: none;
  border-right: none;
  padding-top: 0;
  padding-bottom: 0;
  box-shadow: none;
  border-radius: 0;
  background-color: transparent;
}

.form-select {
  box-shadow: none;
}
[data-bs-theme=light] .form-select {
  border-color: #d8e2ee;
  color: #9da9bb;
}
[data-bs-theme=dark] .form-select {
  color: #5e6e82;
}

/* icons on form inputs */
.form-control-wrapper-icon {
  position: relative;
}
.form-control-wrapper-icon .form-control-icon {
  position: absolute;
  right: 15px;
  top: 50%;
  transform: translateY(-50%);
  color: #8a8a8a;
  pointer-events: none;
}

/*forms on editable cards*/
.card-editable .btn-edit {
  display: none;
}
.card-editable .btn-save {
  display: none;
}
.card-editable[data-mode=view] .btn-edit {
  display: block;
}
.card-editable[data-mode=edit] .btn-save {
  display: block;
}
.card-editable .data-edit:not(.active) {
  display: none;
}
.card-editable .data-view:not(.active) {
  display: none;
}

/* -------------------- Buttons ------------------- */
.btn.btn-dquam-primary {
  border-color: transparent;
  box-shadow: none;
  background-color: #7e55a4;
  color: #fff;
}
.btn.btn-dquam-primary:hover {
  background-color: #552482;
}
.btn.btn-dquam-primary-light {
  border-color: transparent;
  box-shadow: none;
  background-color: #a271cf;
  color: #fff;
}
.btn.btn-dquam-primary-light:hover {
  background-color: #7e55a4;
}
.btn.btn-dquam-primary-emphasis {
  border-color: transparent;
  box-shadow: none;
  background-color: #552482;
  color: #fff;
}
.btn.btn-dquam-primary-emphasis:hover {
  background-color: #7e55a4;
}
.btn.btn-dquam-success {
  border-color: transparent;
  box-shadow: none;
  background-color: #29c394;
  color: #fff;
}
.btn.btn-dquam-success:hover {
  background-color: #20ad83;
}
.btn.btn-dquam-info {
  border-color: transparent;
  box-shadow: none;
  background-color: #1869d2;
  color: #fff;
}
.btn.btn-dquam-info:hover {
  background-color: #115dc2;
}
.btn.btn-dquam-danger {
  border-color: transparent;
  box-shadow: none;
  background-color: #e63757;
  color: #fff;
}
.btn.btn-dquam-danger:hover {
  background-color: #d42949;
}
.btn.btn-dquam-secondary {
  border-color: transparent;
  box-shadow: none;
  background-color: #9cabbf;
  color: #fff;
}
.btn.btn-dquam-secondary:hover {
  background-color: #748194;
}

[data-bs-theme=light] .btn.btn-link:focus {
  background-color: #e6e6e6;
}

[data-bs-theme=dark] .btn.btn-link:focus {
  background-color: #0a1523;
}

.btn-close-falcon:before, .btn-close-falcon::after {
  background-color: #e63757;
}

/* ========================================================================================
Configuration Pages
=========================================================================================*/
/* ---------- Steps Wizard ---------- */
.card-header-config-stages .nav-item {
  width: 33.33%;
  padding: 0;
}
.card-header-config-stages .nav-item .nav-link {
  color: rgba(126, 85, 164, 0.35) !important;
}
.card-header-config-stages .nav-item .nav-link.active {
  color: #7e55a4 !important;
  background-color: transparent;
}
.card-header-config-stages .nav-item .nav-link.active .nav-item-circle-parent:after {
  background-color: #748194;
}
.card-header-config-stages .nav-item .nav-link.done {
  color: #748194 !important;
  background-color: transparent;
}
.card-header-config-stages .nav-item .nav-link.done .nav-item-circle-parent .nav-item-circle {
  background-color: #748194 !important;
  border: #748194;
}
.card-header-config-stages .nav-item .nav-link.done .nav-item-circle-parent:before {
  background-color: #748194;
}